html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

span {
  font-size: 30px;
  padding-left: 10px; }

.home {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 0.3fr 0.2fr auto;
  grid-template-areas: 'welcome' 'information' 'release'; }
  .home .welcome {
    grid-area: welcome; }
  .home .information {
    grid-area: information; }
  .home .platform {
    grid-area: platform; }
  .home .release {
    grid-area: release; }

.release {
  padding: 15px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  grid-template-areas: 'platform release';
  grid-gap: 15px; }
  .release .platform-list {
    grid-area: platform; }
  .release .release-list {
    grid-area: release; }
  .release .platform-list,
  .release .release-list {
    background-color: #282c3491;
    color: white; }
    .release .platform-list li,
    .release .release-list li {
      list-style: none; }

.app-main {
  grid-area: main; }

.main-content {
  height: 100%; }

.app {
  text-align: center;
  display: grid;
  height: 100%;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1.8fr;
  grid-template-areas: 'header' 'main'; }

.app-logo {
  height: 40px;
  pointer-events: none; }

.app-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  color: white;
  padding: 5px;
  grid-area: header; }

.app-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

